<template>

	<div class="body">
		<h3> </h3>
		<br>
		<div class="row">
			<div class="col-1 fw-bolder lh-lg text-center"></div>
			<div class="col-1" style="width: 13%; flex: 0 0 13%;max-width: 13%;">
				<select class="form-select" id="addon-wrapping" v-model="searchStts" @change="search">
					<option value=""></option>
					<option value="e0"></option>
					<option value="e1"></option>
					<option value="e4"></option>
					<option value="e8"></option>
				</select>
			</div>
			<div class="col-1 fw-bolder lh-lg text-center"></div>
			<div class="col-1" style="width: 14%; flex: 0 0 14%;max-width: 14%;">
				<input type="date" class="form-select" v-model="searchStr" @change="search">
			</div>
		</div>
		<hr>
		
		<div class="row">
			<div class="col"></div>
			<div class="col-1 text-end fw-bolder lh-lg"  style="width: 14%; flex: 0 0 14%;max-width: 14%;"> </div>
			<div class="col-1" style="width: 13%; flex: 0 0 14%;max-width: 13%;">
				<select class="form-select"  v-model="stts">
					<option selected value=""></option>
					<option v-for="reqStts in regSttsList" :value="reqStts.cmmnDetaCode">{{reqStts.codeDesct}}</option>
				</select>
			</div>
			<div class="col-1">
				<button @click="changeBtn" class="btn btn-success mb-3"></button>
			</div>
		</div>
		<v-data-table
				v-model="selected"
				v-model:page="page"
    		v-model:items-per-page="itemsPerPage"
    		:headers="headers"
    		:items="boardList"
   		  item-value="boardNo"
				no-data-text="  "
				return-object
    		show-select
				hide-default-footer
				@click:row=info
   		 class="elevation-1"
				>

				<template v-slot:bottom>
      		<div class="text-center pt-2">
        		<v-pagination v-model="page" :length="pageCount"></v-pagination>
        	</div>
    		</template>
			
			</v-data-table>
				
			<!--  -->
  <div id="myModal" class="modal">
      <!--   -->
      <div class="modal-content">
        <span class="close">&times;</span>
        <div v-if="Object.keys(board).length>0">
          <div>
						<table class="table" style="margin-bottom: 0px;">
							<tbody>
								<tr>
									<th></th><td v-text="board.boardNo"></td>
									<th></th><td v-text="board.regDate"></td>
									<th></th><td v-text="board.memNo"></td>
								</tr>
								<tr>
									<th></th><td colspan="3" v-text="board.title"></td>
									<th></th><td v-text="board.shipStrDate"></td>
								</tr>
							</tbody>
						</table>
						<table class="table">
							<tbody>
								<tr>
									<th></th><td v-text="formatNumber(board.goalPrice)"></td>
									<th></th><td>{{ formatNumber(board.fndPrice) }}</td>
								</tr>
								<tr>
									<th></th><td v-text="board.fndStrDate"></td>
									<th></th><td v-text="board.shipStrDate"></td>
								</tr>
								<tr>
									<th></th><td colspan="3" class="desct" v-html="board.desct"></td>
								</tr>
							</tbody>
						</table>
            <div class="text-end">
              <div v-if="board.regStts==' '" >
                <button v-show="btnShow" class="btn btn-success mb-3 mx-3" @click="apply"></button>
                <select v-model="reason" v-show="!btnShow">
                  <option value="   ">   </option>
                  <option value="  "> </option>
                </select>
                <button class="btn btn-danger mb-3 mx-3" @click="refusal1" v-show="btnShow"></button>
                <button class="btn btn-danger mb-3 mx-3" @click="refusal2" v-show="!btnShow"></button>
              </div>
              <div v-else>
                  <button class="btn btn-primary mb-3 mx-3" @click="back"></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
		
	</div>

</template>


<script>

import { VDataTable } from 'vuetify/labs/VDataTable'
import axios from 'axios'
export default{
  data(){
    return{
			searchStts:'e0',
			searchStr:'',searchEnd:'',
			btnShow:true,reason:'',page:1,
			selected:[],itemsPerPage: 10,
			board:{}, boardList:[],
			dst1:'',dst1List:[],
			dst2:'',dst2List:[],
      dst2All:{},regSttsList:[],stts:'',
      headers:[
	        {title: '',key: 'boardNo',align: 'center'},
	        {title: '', key: 'title'},
					{title: '', key: 'fndStrDate',align: 'center'},
					{title: '',key: 'fndEndDate',align: 'center' },
          {title: '',key: 'regDate',align: 'center' },
	        {title: '',key: 'regStts',align: 'center'}
	      ]
    }
  },
	components: {
      VDataTable,
    },
methods:{
	search(){
		let obj={}
		obj.stts=this.searchStts;
		obj.str=this.searchStr;
		obj.end=this.searchEnd;
		this.callList(obj);
	},
	changeBtn(){
		if(this.stts==''){
			this.$swal({
      	title: " ",
      	icon: "warning",
      	showConfirmButton: false,
				timer: 1500
    	});
			return;
		}
		let list =[];
		this.selected.forEach(item => {
			let obj={boardNo: item.boardNo, 
          reqStts:this.stts,
					tableName:'funding'}
			    list.push(obj);
		  });
		this.modify(list)
	},
	modify(list){
  	axios.put('/admin/chageRegStatus', list)
  	.then(response => {
			let stts= this.searchStts
			this.callList({stts})
			this.selected = [];
    	this.stts = '';
    	this.board = {};
  	})
  	.catch(err => console.log(err))
  	.finally(() => this.back())
	},
	info(event,item){
		let index=item.item.index;
		this.board= this.boardList[index];
		this.onpenModal()
	},
	onpenModal(){
		document.getElementById("myModal").style.display = "block";	
	},
	apply(){
		let list=[];
		list.push({boardNo:this.board.boardNo,reqStts:'e1',tableName:'funding'});
		this.modify(list);
	},
	refusal1(){
		this.btnShow=false;
	},
	refusal2(){
		let obj ={
			boardNo : this.board.boardNo,
			memNo: this.board.memNo,
			alrtTitle: ' ',
			alrtDesct: this.reason,
			boardCtg: 'g0e',
			tableName:'funding',
			reqStts:'e8',

		}
		let list = [obj];
		this.modify(list)
		axios.post('/admin/rejectAlert', obj, {
  		headers: {
    		'Content-Type': 'application/json',
  		}
		})
		.then((response) => {
  		if (response.data.retCode == "Success") {
    		this.$swal({
					title: "   .",
					icon: "success",
					showConfirmButton: false,
					timer: 1500
				});
  		} else {
    		this.$swal({
      		title: "  .",
      		icon: "error",
      		showConfirmButton: false,
      		timer: 1500
    		});
  		}})
			.catch((err) => console.log(err))
			.finally(() => this.back());
	},
	back(){
		document.getElementById("myModal").style.display = "none";
	},
	callList(vo){
		axios.get("/admin/fundings",{params: vo})
		.then(response => {
			this.boardList = response.data;
		})
		.catch(err => console.log(err));
	},
	formatNumber(number) {
		if (number === null || number === undefined) {
        return '';
    }
      return number.toLocaleString();
  }
},
  mounted(){
		this.dst1List = this.$store.state.dst1;
		this.regSttsList = this.$store.state.regSttsList;
		this.dst2All = this.$store.state.des2All;
		
		this.callList({stts:'e0'});
			
		// 
		
		window.onclick = function(event) {
  		if (event.target == document.getElementById("myModal")) {
				document.getElementById("myModal").style.display = "none";
				this.board={};
  		}
		}
		document.getElementsByClassName("close")[0].addEventListener('click',function(){
			document.getElementById("myModal").style.display = "none";
			this.board={};
		})
  },
	computed: {
      pageCount () {
        return Math.ceil(this.boardList.length / this.itemsPerPage)
      },
  },
	created() {
    document.title = "";
  }
}

</script>

<style>

</style>